/* base styles */

*{
    margin: 0;
    font-family: 'Courier New', Courier, monospace;
    color: #333;
}

.navbar{
    padding: 20px;
    display: flex;
    align-items: center;
    margin: 0 auto;
    border-bottom: 1px solid #f2f2f2;
    max-width: 600px;
}

.navbar h1{
    color: #f1356d;
}

.navabar .links{
    margin-left:auto;
} 

.navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.navbar a:hover{
    color:#f1356d;
    transition: 0.4s ease-in-out;
}

.content{
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
}

/* blog preview / lists */
.blog-preview{
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 1px solid #fafafa;
}

.blog-preview:hover{
    box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.blog-preview{
    font-size: 20px;
    color:#f1356d;
    margin-bottom: 8px;
}

.blog-preview a{
    text-decoration: none;
}

/* blog details page */
.blog-details h2{
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 10px;
}

.blog-details div{
    margin: 20px 0;
}

.create{
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.create label{
    text-align: left;
    display: block;
}

.create h2{
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 30px;
}

.create input, .create textarea, .create select{
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
}

.create button{
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}

.blog-details button{
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}

.not-found h2{
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 10px;
}